.navbar{
    background-color: #cac7c7;
    height: 3rem;
    display: flex;
    width: 100vw;
    margin: 0px;
    align-items: center;
    justify-content: space-around;
    left: 0px;
    top: 0px;
    position: fixed;
}

.navbar ul {
    display: flex;
    justify-content: flex-start;
}
.navbar ul li{
    display: flex;
    padding: 13px;
    justify-content: flex-start;
    transition: .4s;
}

.navbar ul li:hover{
    background-color: cyan;
}