.new-issue{
    margin-top: 40px;
    padding: 2px;
    background-color: rgb(78, 162, 241);
   

}   
.new-issue .input{
    display: inline-block;
    padding: 10px 10px;
}

.new-issue .input .input-text {
    font-size: 16px;
}

.new-issue .input .button {
    font-size: 16px;
    border: none;
    padding: 8px;
    background-color: #686464;
    color: #aca8a8;
    transition: .3s;
}

.new-issue .input .button:hover{
    background-color: #aca8a8;
    color: rgb(56, 4, 245);
}